import React, { useState } from "react";
import styles from "./signup.module.css";
import axios from "axios";

const Signup = (props) => {
  const [checkedId, setCheckedId] = useState(null);
  const [checkedPhone, setCheckedPhone] = useState(null);
  const [disable, setDisable] = useState(false);
  const [checkValues, setCheckValues] = useState({
    signupAgree: false,
    infoCollectionAgree: false,
  });

  const [inputValues, setInputValues] = useState({
    id: "",
    pw: "",
    pwConfirm: "",
    name: "",
    phone: "",
    authNum: "",
  });

  const { id, pw, pwConfirm, name, phone, authNum } = inputValues;

  const { signupAgree, infoCollectionAgree } = checkValues;

  const inputValueChangeHandler = (e) => {
    const { name, value } = e.target;
    setInputValues({
      ...inputValues,
      [name]: value,
    });
  };

  const checkValueChangeHandler = (e) => {
    const { name, checked } = e.target;
    setCheckValues({
      ...checkValues,
      [name]: checked,
    });
  };

  const idDupCheckHandler = () => {
    if (id.length < 4 || id.length > 12) {
      window.alert(" 4~12 ");
      return;
    }
    axios
      .post(`${process.env.REACT_APP_BASEURL}/user/useridvalid`, {
        id,
      })
      .then((response) => {
        if (response.data === "OK") {
          window.alert("  .");
          setCheckedId(id);
          return;
        }
        window.alert("  .");
      })
      .catch((err) => console.error(err));
  };

  const allAgreeHandler = (e) => {
    if (e.target.checked) {
      setCheckValues({
        signupAgree: true,
        infoCollectionAgree: true,
      });
    } else {
      setCheckValues({
        signupAgree: false,
        infoCollectionAgree: false,
      });
    }
  };

  const sendSmsHandler = () => {
    if (phone === "") {
      window.alert("   ");
      return;
    }
    if (phone.length !== 11) {
      window.alert("   ");
      return;
    }
    for (let i = 0; i < phone.length; i++) {
      if (isNaN(parseInt(phone.charAt(i)))) {
        window.alert(" ");
        return;
      }
    }
    axios
      .post(`${process.env.REACT_APP_BASEURL}/user/userphonevalid`, {
        phone,
      })
      .then((response) => {
        if (response.data !== "OK") {
          window.alert("   .");
          return;
        }
        axios
          .post(`${process.env.REACT_APP_BASEURL}/user/requestsms`, {
            phone,
          })
          .then((response) => window.alert(" ."))
          .catch((err) => console.error(err));
      })
      .catch((err) => console.error(err));
  };

  const authNumCheckHandler = () => {
    axios
      .post(`${process.env.REACT_APP_BASEURL}/user/checksms`, {
        to: phone,
        content: authNum,
      })
      .then((response) => {
        if (response.data === "success") {
          window.alert(" .");
          setCheckedPhone(phone);
          setDisable(true);
          return;
        }
        window.alert(" .");
      });
  };

  const signupSubmitHandler = (e) => {
    e.preventDefault();

    if (!(signupAgree && infoCollectionAgree)) {
      window.alert("    .");
      return;
    }

    if (
      id.length === 0 ||
      pw.length === 0 ||
      name.length === 0 ||
      phone.length === 0
    ) {
      window.alert("   .  .");
      return;
    }

    if (pw !== pwConfirm) {
      window.alert("    .");
      return;
    }

    if (pw.length < 8 || pw.length > 16) {
      window.alert(" 8~16 .");
    }

    if (!checkedId) {
      window.alert("   .");
    }

    if (!checkedPhone) {
      window.alert("   .");
      return;
    }

    axios
      .post(`${process.env.REACT_APP_BASEURL}/user/register`, {
        id: checkedId,
        password: pw,
        name,
        phone: checkedPhone,
      })
      .then((response) => {
        const resData = response.data;
        if (resData === "success") {
          window.alert(" .");
          window.location.href = "/";
        }
      })
      .catch((error) => console.error(error));
  };
  return (
    <section className={styles.signup}>
      <form className={styles.signup_form}>
        <div className={styles.signup_title_container}>
          <h1 className={styles.signup_title}> </h1>
        </div>

        <section className={styles.input_part}>
          <div className={styles.input_container}>
            <p className={styles.input_title}></p>
            <input
              name="id"
              onChange={inputValueChangeHandler}
              value={id}
              type="id"
              className={`${styles.input} ${styles.phone_num_input}`}
              placeholder=" (4~12)"
              spellCheck="false"
            />
            <button
              type="button"
              className={styles.get_auth_num_button}
              onClick={idDupCheckHandler}
            >
              
            </button>
          </div>
          <div className={styles.input_container}>
            <p className={styles.input_title}></p>
            <input
              name="pw"
              onChange={inputValueChangeHandler}
              value={pw}
              type="password"
              className={`${styles.input} ${styles.password_input}`}
              placeholder=" (8~16)"
              spellCheck="false"
            />
          </div>
          <div className={styles.input_container}>
            <p className={styles.input_title}> </p>
            <input
              name="pwConfirm"
              onChange={inputValueChangeHandler}
              value={pwConfirm}
              type="password"
              className={`${styles.input} ${styles.password_confirm_input}`}
              placeholder=" "
              spellCheck="false"
            />
          </div>
          <div className={styles.input_container}>
            <p className={styles.input_title}></p>
            <input
              name="name"
              onChange={inputValueChangeHandler}
              value={name}
              type="text"
              className={`${styles.input} ${styles.name_input}`}
              placeholder=""
              spellCheck="false"
            />
          </div>

          <div className={styles.input_container}>
            <p className={styles.input_title}> </p>
            <input
              name="phone"
              onChange={inputValueChangeHandler}
              value={phone}
              type="text"
              className={`${styles.input} ${styles.phone_num_input}`}
              placeholder=" "
              spellCheck="false"
              disabled={disable}
            />
            <button
              type="button"
              className={styles.get_auth_num_button}
              onClick={sendSmsHandler}
            >
               
            </button>
          </div>
          <div className={styles.input_container}>
            <p className={styles.input_title}></p>
            <input
              name="authNum"
              onChange={inputValueChangeHandler}
              value={authNum}
              type="text"
              className={`${styles.input} ${styles.phone_num_input}`}
              placeholder=""
              spellCheck="false"
              disabled={disable}
            />
            <button
              type="button"
              className={styles.get_auth_num_button}
              onClick={authNumCheckHandler}
            >
               
            </button>
          </div>
        </section>
        <section className={styles.agree_part}>
          <div className={styles.all_agree}>
            <input
              type="checkbox"
              className={styles.agree_input}
              onChange={allAgreeHandler}
            />
            <span className={styles.all_agree_desc}>  </span>
          </div>
          <div className={styles.separate_agree_container}>
            <div
              className={`${styles.separate_agree} ${styles.separate_agree_top}`}
            >
              <input
                name="signupAgree"
                onChange={checkValueChangeHandler}
                value={signupAgree}
                type="checkbox"
                checked={signupAgree}
                className={styles.agree_input}
              />
              <span className={styles.agree_desc}>
                     ()
              </span>
              <i className={`${styles.icon} fas fa-chevron-right`}></i>
            </div>
            <div
              className={`${styles.separate_agree} ${styles.separate_agree_bottom}`}
            >
              <input
                name="infoCollectionAgree"
                onChange={checkValueChangeHandler}
                value={infoCollectionAgree}
                type="checkbox"
                checked={infoCollectionAgree}
                className={styles.agree_input}
              />
              <span className={styles.agree_desc}>
                    ()
              </span>
              <i className={`${styles.icon} fas fa-chevron-right`}></i>
            </div>
          </div>
        </section>
        <button className={styles.submit_button} onClick={signupSubmitHandler}>
          
        </button>
      </form>
    </section>
  );
};

export default Signup;
